<template>
	<view>
		<u-tabbar :value="tabValue" @change="changeTab => tabValue = changeTab" :fixed="true" :placeholder="true"
			activeColor="#00b341" :safeAreaInsetBottom="true">
			<u-tabbar-item text="首页1" @click="switchTab('home')">
				<image class="u-page__item__slot-icon" slot="inactive-icon"
					:src="currentTab === 'home' ? homeActiveIcon : homeIcon">
				</image>
			</u-tabbar-item>
			<u-tabbar-item text="分类1" @click="switchTab('classify')">
				<image class="u-page__item__slot-icon" slot="inactive-icon"
					:src="currentTab === 'classify' ? classifyActiveIcon : classifyIcon">
				</image>
			</u-tabbar-item>
			<u-tabbar-item text="我的1" @click="switchTab('myself')">
				<image class="u-page__item__slot-icon" slot="inactive-icon"
					:src="currentTab === 'myself' ? myselfActiveIcon : myselfIcon">
				</image>
			</u-tabbar-item>
		</u-tabbar>
	</view>
</template>

<script>
	export default {
		props: {
			currentTab: {
				type: String,
				default: 'home',
			},
		},
		data() {
			return {
				tabValue: 0,
				homeIcon: 'https://blog-1255855400.cos.ap-guangzhou.myqcloud.com/svg/home.svg',
				homeActiveIcon: 'https://blog-1255855400.cos.ap-guangzhou.myqcloud.com/svg/on-home.svg',
				classifyIcon: 'https://blog-1255855400.cos.ap-guangzhou.myqcloud.com/svg/classify.svg',
				classifyActiveIcon: 'https://blog-1255855400.cos.ap-guangzhou.myqcloud.com/svg/on-classify.svg',
				myselfIcon: 'https://blog-1255855400.cos.ap-guangzhou.myqcloud.com/svg/me.svg',
				myselfActiveIcon: 'https://blog-1255855400.cos.ap-guangzhou.myqcloud.com/svg/on-me.svg',
			}
		},
		onLoad() {

		},
		methods: {
			switchTab(tab) {
				console.log("tab3:", tab);
				this.$emit('change-tab', tab);
			},
		}
	}
</script>

<style scoped>
	.u-page__item__slot-icon {
		width: 150rpx;
		height: 50rpx;
	}
</style>